import React, { Fragment, useState } from "react";

import Message from "./Message";
import Progress from "./Progress";
import axios from "axios";

const FormUpload = () => {
  //logica del componente
  const [file, setFile] = useState("");
  const [filename, setFilename] = useState("Elegir archivo para presentar");
  const [uploadedFile, setUploadedFile] = useState({});
  const [filesList, setFilesList] = useState([]);
  const [message, setMessage] = useState("");
  const [uploadPercentage, setUploadPercentage] = useState(0);

  const onChange = (e) => {
    setFile(e.target.files[0]);
    setFilename(e.target.files[0].name);
  };

  //
  const getFiles = async (e) => {
    e.preventDefault();
    try {
      const files = await axios.get("/upload");
      console.log(files.data);
      setFilesList(files.data);
    } catch (error) {
      console.log(error);
    }
  };
  //
  const onSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("file", file);

    try {
      const res = await axios.post("/upload", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
        onUploadProgress: (progressEvent) => {
          setUploadPercentage(
            parseInt(
              Math.round((progressEvent.loaded * 100) / progressEvent.total)
            )
          );

          // Clear percentage
          setTimeout(() => setUploadPercentage(0), 10000);
        },
      });

      const { fileName, filePath } = res.data;
      console.log(uploadedFile);
      setUploadedFile({ fileName, filePath });

      setMessage("Archivo Subido");
    } catch (err) {
      if (err.response.status === 500) {
        setMessage("There was a problem with the server");
      } else {
        setMessage(err.response.data.msg);
      }
    }
  };
  //
  return (
    <Fragment>
      {message ? <Message msg={message} /> : null}
      <form onSubmit={onSubmit}>
        <div className="custom-file mb-4">
          <input
            type="file"
            className="custom-file-input"
            id="customFile"
            onChange={onChange}
          />
          <label className="custom-file-label" htmlFor="customFile">
            {filename}
          </label>
        </div>

        <Progress percentage={uploadPercentage} />

        <div className="container">
          <input
            type="submit"
            value="Click aquí para subir su documento"
            className="btn btn-primary w-50 mx-auto btn-block mt-4"
          />
        </div>
      </form>
      <form onSubmit={getFiles}>
        <input
          type="submit"
          value="Click aquí para ver el listado de archivos subidos"
          className="btn btn-secondary w-50 mx-auto btn-block mt-4"
        />
      </form>

      <div className="container my-2">
        <h6 className="text-center">listado de archivos </h6>
      </div>

      {filesList ? (
        <div className="col-md-9 m-auto">
          <table className="table">
            <caption>Presentación Documental</caption>
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Nombre Del documento</th>
                <th scope="col">Enlace de descarga</th>
                <th scope="col">Fecha de entrega</th>
                <th scope="col">Fecha de subida</th>
              </tr>
            </thead>
            <tbody>
              {filesList.map((file) => (
                <tr>
                  <th scope="row">1</th>
                  <td>{file.fileName}</td>
                  <td>
                    <a
                      href={file.Path}
                      target="_blank"
                      rel="noopener
                  no referrer"
                    >
                      {file.fileName}
                    </a>
                  </td>
                  <td>...</td>
                  <td>...</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : null}
    </Fragment>
  );
};

export default FormUpload;
