import React, { Fragment } from "react";

const Filter = () => {
  return (
    <Fragment>
      <div className="container my-5 border border-info rounded-pill">
        <div className="row row-cols-3 center">
          <div className="col text-center">
            <select className="custom-select custom-select-sm my-3 alert-info">
              <option selected>Seleccione un año</option>
              <option value="2019">2019</option>
              <option value="2020">2020</option>
            </select>
          </div>
          <div className="col text-center">
            <select className="custom-select custom-select-sm my-3 alert-success">
              <option selected>Seleccione el mes/anual</option>
              <option value="Enero">Enero</option>
              <option value="Febrero">Febrero</option>
              <option value="Marzo">Marzo</option>
              <option value="Abril">Abril</option>
              <option value="Mayo">Mayo</option>
              <option value="Junio">Junio</option>
              <option value="Julio">Julio</option>
              <option value="Agosto">Agosto</option>
              <option value="Septiembre">Septiembre</option>
              <option value="Octubre">Octubre</option>
              <option value="Noviembre">Noviembre</option>
              <option value="Diciembre">Diciembre</option>
              <option value="Anual">Anual</option>
            </select>
          </div>
          <div className="col text-center">
            <form className="form-inline">
              <div className="form-check my-3">
                <label className="form-check-label mr-2">
                  <input type="checkbox" className="form-check-input" />
                  Fijar mi selección
                </label>
              </div>
              <button
                className="btn btn-light mx-auto alert-primary"
                type="submit"
              >
                Click aquí para Filtrar
              </button>
            </form>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default Filter;
