import React, { Fragment } from "react";

const NavBar = () => {
  return (
    <Fragment>
      <nav className="navbar navbar-expand-lg navbar-light bg-light my-4">
        <a className="navbar-brand" href="#">
          HTC - 2020
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarColor03"
          aria-controls="navbarColor03"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarColor03">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item active">
              <a className="nav-link" href="#">
                Inicio <span className="sr-only">(current)</span>
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="mis_presentaciones.html">
                Mis Presentaciones
              </a>
            </li>

            <li className="nav-item">
              <a className="nav-link" href="#">
                Expedientes de Actuaciones
              </a>
            </li>
            <li className="nav-item">
              <a
                className="nav-link"
                target="_blank"
                rel="noopener
               no referrer"
                href="localhost:3000"
              >
                Presentación Documental
              </a>
            </li>
          </ul>

          <button type="button" className="mx-2 btn btn-primary">
            Notificaciones <span className="badge badge-light">4</span>
          </button>
          <a
            className="btn btn-warning ml-5 mr-2"
            href="../index.html"
            role="button"
          >
            Cerrar Sesión
          </a>
        </div>
      </nav>
    </Fragment>
  );
};

export default NavBar;
